.recovery_utils {
    display: flex;
    align-items: center;
    padding-bottom: 3rem;
    align-self: flex-start;
}

.back_btn {
    background: transparent;
    width: 2rem;
    height: 2rem;
    padding: 0;
    border: 0;
    cursor: pointer;
}

.resetPassword-title {
    font-size: 2rem;
    font-weight: 400;
    line-height: 2.5rem;
    padding-left: 1rem;
    color: #FEFFB3;
}

.resetPassword-paragraph {
    color: #FEFFB3;
    font-weight: 300;
    font-size: 1.5rem;
    letter-spacing: .25rem;
    line-height: 1.5rem;
    margin-bottom: 3rem;
    text-wrap: nowrap;
}

.input {
    background: transparent;
    border: 2px solid #FBE0F3;
    outline: none;
    min-height: 4.625rem;
    height: 100%;
    width: 100%;
    font-size: 1.5rem;
    color: #FBE0F3;
    padding: 0 1.5rem 0 1.5rem;
    margin-bottom: 1rem;
}

.input::placeholder {
    color: rgba(251, 224, 243, 0.5) ;
    font-size: 1rem;
    line-height: 1rem;
    font-weight: 400;
    text-align: start;
    border: 0;
    letter-spacing: 4px;
    padding: 1.875rem 0 1.875rem 0;
}

.btn {
    padding: 1rem 6rem 1rem 6rem;
    font-size: 1.5rem;
    font-weight: 200;
    line-height: 1.75rem;
    background: transparent;
    border: 2px solid #FBE0F3;
    color: #FBE0F3;
    cursor: pointer;
    letter-spacing: .25rem;
    display: flex;
    align-items: center;
    border-radius: 10rem;
    margin-top: 2.5rem;
    margin-bottom: 2.5rem;
}

.btn svg {
    margin-left: 1.5rem;
}

.recover-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: fit-content;
    height: fit-content;
}

.recover-form {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.btn_wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.error {
    color: #EB443C;
    max-height: 0;
    height: 100%;
    overflow: hidden;
    transition: max-height .3s ease-in-out, opacity .3s ease-in-out;
    font-size: 1rem;
    padding: 0;
    margin: 0;
    font-weight: 400;
    letter-spacing: 4px;
    opacity: 0;
    text-align: center;
    line-height: 1.5rem;
    white-space: pre-line;
    margin-bottom: 1rem;
}

.error_border {
    border-color: rgba(201, 42, 21, 0.85);  
}

.visible {
    opacity: 1;
    max-height: 1.5rem;
}